/**
 * Specs Management page component for managing OS specifications.
 */

import { useState } from 'react';
import { Link } from 'react-router-dom';
import {
  Plus,
  Search,
  Filter,
  RefreshCw,
  Edit,
  Trash2,
  Download,
  Upload,
  FileText,
  Settings
} from 'lucide-react';
import { 
  useSpecs, 
  useCreateSpec, 
  useUpdateSpec, 
  useDeleteSpec,
  useValidateSpec 
} from '@/hooks/api-hooks';
import { LoadingState, LoadingButton } from '@/components/ui/loading';
import { formatDate, cn } from '@/lib/utils';
import type { Spec } from '@/types/api';

export function SpecsPage() {
  const [searchTerm, setSearchTerm] = useState('');
  const [osTypeFilter, setOsTypeFilter] = useState<string>('all');
  const [showCreateModal, setShowCreateModal] = useState(false);
  const [editingSpec, setEditingSpec] = useState<Spec | null>(null);

  // API hooks
  const { data: specs, isLoading, refetch } = useSpecs();
  const deleteSpec = useDeleteSpec();

  if (isLoading) {
    return <LoadingState message="Loading specifications..." />;
  }

  // Filter specs
  const filteredSpecs = specs?.filter(spec => {
    const matchesSearch = !searchTerm || 
      spec.name.toLowerCase().includes(searchTerm.toLowerCase()) ||
      (spec.description && spec.description.toLowerCase().includes(searchTerm.toLowerCase())) ||
      spec.os_type.toLowerCase().includes(searchTerm.toLowerCase());
    
    const matchesOsType = osTypeFilter === 'all' || spec.os_type === osTypeFilter;
    
    return matchesSearch && matchesOsType;
  }) || [];

  // Get unique OS types for filter
  const osTypes = [...new Set(specs?.map(spec => spec.os_type) || [])];

  const handleDeleteSpec = async (specName: string) => {
    if (window.confirm(`Are you sure you want to delete the spec "${specName}"? This action cannot be undone.`)) {
      try {
        await deleteSpec.mutateAsync(specName);
      } catch (error) {
        // Error handled by mutation
      }
    }
  };

  return (
    <div className="space-y-6">
      {/* Header */}
      <div className="flex flex-col sm:flex-row justify-between items-start sm:items-center gap-4">
        <div>
          <h1 className="text-3xl font-bold text-gray-900">Specifications</h1>
          <p className="text-gray-600 mt-1">
            Manage OS image specifications and configurations
          </p>
        </div>
        <div className="flex space-x-3">
          <button
            onClick={() => setShowCreateModal(true)}
            className="btn-primary"
          >
            <Plus className="w-4 h-4 mr-2" />
            New Spec
          </button>
        </div>
      </div>

      {/* Filters and search */}
      <div className="card p-6">
        <div className="flex flex-col lg:flex-row gap-4">
          {/* Search */}
          <div className="flex-1">
            <div className="relative">
              <Search className="w-5 h-5 absolute left-3 top-1/2 transform -translate-y-1/2 text-gray-400" />
              <input
                type="text"
                placeholder="Search specs by name, description, or OS type..."
                className="input pl-10"
                value={searchTerm}
                onChange={(e) => setSearchTerm(e.target.value)}
              />
            </div>
          </div>

          {/* OS Type filter */}
          <div className="flex items-center space-x-2">
            <Filter className="w-4 h-4 text-gray-500" />
            <select
              className="input w-auto"
              value={osTypeFilter}
              onChange={(e) => setOsTypeFilter(e.target.value)}
            >
              <option value="all">All OS Types</option>
              {osTypes.map((osType) => (
                <option key={osType} value={osType}>
                  {osType}
                </option>
              ))}
            </select>
          </div>

          {/* Refresh button */}
          <button
            onClick={() => refetch()}
            className="btn-secondary"
            disabled={isLoading}
          >
            <RefreshCw className={`w-4 h-4 ${isLoading ? 'animate-spin' : ''}`} />
          </button>
        </div>
      </div>

      {/* Stats */}
      <div className="grid grid-cols-1 sm:grid-cols-3 gap-4">
        <div className="card p-4">
          <div className="text-center">
            <p className="text-2xl font-bold text-gray-900">{specs?.length || 0}</p>
            <p className="text-sm text-gray-600">Total Specs</p>
          </div>
        </div>
        <div className="card p-4">
          <div className="text-center">
            <p className="text-2xl font-bold text-primary-600">{osTypes.length}</p>
            <p className="text-sm text-gray-600">OS Types</p>
          </div>
        </div>
        <div className="card p-4">
          <div className="text-center">
            <p className="text-2xl font-bold text-gray-900">{filteredSpecs.length}</p>
            <p className="text-sm text-gray-600">Filtered Results</p>
          </div>
        </div>
      </div>

      {/* Specs grid */}
      <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
        {filteredSpecs.length === 0 ? (
          <div className="col-span-full">
            <div className="text-center py-12">
              <FileText className="w-16 h-16 text-gray-300 mx-auto mb-4" />
              <h3 className="text-lg font-medium text-gray-900 mb-2">
                {searchTerm || osTypeFilter !== 'all' ? 'No specs match your filters' : 'No specs found'}
              </h3>
              <p className="text-gray-600 mb-6">
                {searchTerm || osTypeFilter !== 'all' 
                  ? 'Try adjusting your search or filter criteria'
                  : 'Get started by creating your first OS specification'
                }
              </p>
              {(!searchTerm && osTypeFilter === 'all') && (
                <button
                  onClick={() => setShowCreateModal(true)}
                  className="btn-primary"
                >
                  <Plus className="w-4 h-4 mr-2" />
                  Create First Spec
                </button>
              )}
            </div>
          </div>
        ) : (
          filteredSpecs.map((spec) => (
            <SpecCard
              key={spec.name}
              spec={spec}
              onEdit={() => setEditingSpec(spec)}
              onDelete={() => handleDeleteSpec(spec.name)}
              deleteLoading={deleteSpec.isPending}
            />
          ))
        )}
      </div>

      {/* Create/Edit Modal */}
      {(showCreateModal || editingSpec) && (
        <SpecModal
          spec={editingSpec}
          isOpen={true}
          onClose={() => {
            setShowCreateModal(false);
            setEditingSpec(null);
          }}
        />
      )}
    </div>
  );
}

// Spec card component
interface SpecCardProps {
  spec: Spec;
  onEdit: () => void;
  onDelete: () => void;
  deleteLoading: boolean;
}

function SpecCard({ spec, onEdit, onDelete, deleteLoading }: SpecCardProps) {
  return (
    <div className="card p-6 hover:shadow-lg transition-shadow">
      <div className="flex justify-between items-start mb-4">
        <div className="flex-1">
          <h3 className="text-lg font-semibold text-gray-900">{spec.name}</h3>
          <p className="text-sm text-gray-600 mt-1">{spec.os_type}</p>
          {spec.version && (
            <p className="text-xs text-gray-500">Version: {spec.version}</p>
          )}
        </div>
        <div className="flex space-x-2">
          <button
            onClick={onEdit}
            className="p-2 text-gray-600 hover:text-gray-900 hover:bg-gray-100 rounded-md transition-colors"
            title="Edit spec"
          >
            <Edit className="w-4 h-4" />
          </button>
          <LoadingButton
            loading={deleteLoading}
            onClick={onDelete}
            className="p-2 text-gray-600 hover:text-danger-700 hover:bg-danger-50 rounded-md transition-colors"
            title="Delete spec"
          >
            <Trash2 className="w-4 h-4" />
          </LoadingButton>
        </div>
      </div>

      {spec.description && (
        <p className="text-sm text-gray-600 mb-4 line-clamp-3">
          {spec.description}
        </p>
      )}

      <div className="space-y-2 text-xs text-gray-500">
        <div className="flex justify-between">
          <span>Configuration keys:</span>
          <span>{Object.keys(spec.config || {}).length}</span>
        </div>
      </div>

      <div className="mt-4 flex justify-between items-center">
        <Link
          to={`/builds/new?spec=${encodeURIComponent(spec.name)}`}
          className="text-primary-600 hover:text-primary-700 text-sm font-medium"
        >
          Use in build 
        </Link>
        <button
          onClick={() => {
            // Download spec as JSON
            const blob = new Blob([JSON.stringify(spec, null, 2)], { type: 'application/json' });
            const url = URL.createObjectURL(blob);
            const a = document.createElement('a');
            a.href = url;
            a.download = `${spec.name}.json`;
            document.body.appendChild(a);
            a.click();
            document.body.removeChild(a);
            URL.revokeObjectURL(url);
          }}
          className="text-gray-600 hover:text-gray-900"
          title="Download spec"
        >
          <Download className="w-4 h-4" />
        </button>
      </div>
    </div>
  );
}

// Spec modal component
interface SpecModalProps {
  spec: Spec | null;
  isOpen: boolean;
  onClose: () => void;
}

function SpecModal({ spec, isOpen, onClose }: SpecModalProps) {
  const [formData, setFormData] = useState({
    name: spec?.name || '',
    description: spec?.description || '',
    os_type: spec?.os_type || '',
    version: spec?.version || '',
    config: JSON.stringify(spec?.config || {}, null, 2)
  });

  const createSpec = useCreateSpec();
  const updateSpec = useUpdateSpec();
  const validateSpec = useValidateSpec();

  const isEditing = !!spec;

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();

    try {
      const config = JSON.parse(formData.config);
      const specData: Spec = {
        name: formData.name,
        description: formData.description || undefined,
        os_type: formData.os_type,
        version: formData.version || undefined,
        config
      };

      if (isEditing) {
        await updateSpec.mutateAsync({ name: spec.name, spec: specData });
      } else {
        await createSpec.mutateAsync(specData);
      }

      onClose();
    } catch (error) {
      // Error handled by mutations
    }
  };

  if (!isOpen) return null;

  return (
    <div className="fixed inset-0 bg-gray-600 bg-opacity-50 overflow-y-auto h-full w-full z-50">
      <div className="relative top-20 mx-auto p-5 border w-11/12 max-w-4xl shadow-lg rounded-md bg-white">
        <div className="mb-6">
          <h3 className="text-lg font-semibold text-gray-900">
            {isEditing ? 'Edit Specification' : 'Create New Specification'}
          </h3>
        </div>

        <form onSubmit={handleSubmit} className="space-y-6">
          <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
            <div>
              <label className="label">Name</label>
              <input
                type="text"
                className="input"
                value={formData.name}
                onChange={(e) => setFormData({ ...formData, name: e.target.value })}
                required
                disabled={isEditing}
              />
            </div>

            <div>
              <label className="label">OS Type</label>
              <input
                type="text"
                className="input"
                value={formData.os_type}
                onChange={(e) => setFormData({ ...formData, os_type: e.target.value })}
                placeholder="e.g., rhel, ubuntu, windows"
                required
              />
            </div>

            <div>
              <label className="label">Version (optional)</label>
              <input
                type="text"
                className="input"
                value={formData.version}
                onChange={(e) => setFormData({ ...formData, version: e.target.value })}
                placeholder="e.g., 9.0, 22.04"
              />
            </div>

            <div>
              <label className="label">Description (optional)</label>
              <input
                type="text"
                className="input"
                value={formData.description}
                onChange={(e) => setFormData({ ...formData, description: e.target.value })}
                placeholder="Brief description of this spec"
              />
            </div>
          </div>

          <div>
            <label className="label">Configuration (JSON)</label>
            <textarea
              className="input h-64 font-mono text-sm"
              value={formData.config}
              onChange={(e) => setFormData({ ...formData, config: e.target.value })}
              required
            />
          </div>

          <div className="flex justify-end space-x-3">
            <button
              type="button"
              onClick={onClose}
              className="btn-secondary"
            >
              Cancel
            </button>
            <LoadingButton
              loading={createSpec.isPending || updateSpec.isPending}
              type="submit"
              className="btn-primary"
            >
              {isEditing ? 'Update Spec' : 'Create Spec'}
            </LoadingButton>
          </div>
        </form>
      </div>
    </div>
  );
}
