/**
 * New Build page component for creating builds through the UI.
 */

import { useState, useEffect } from 'react';
import { useNavigate, Link } from 'react-router-dom';
import {
  ArrowLeft,
  Plus,
  Settings,
  FileText,
  MapPin,
  Layers,
  Play,
  Eye,
  AlertCircle,
  CheckCircle,
  Info
} from 'lucide-react';
import { 
  useCreateBuild, 
  usePlatforms, 
  useLocations, 
  useSpecs,
  useValidateSpec
} from '@/hooks/api-hooks';
import { LoadingState, LoadingButton } from '@/components/ui/loading';
import { cn, isValidJSON } from '@/lib/utils';
import type { BuildConfig, Platform, Location, Spec } from '@/types/api';

interface BuildFormData {
  platform: string;
  location: string;
  spec: string;
  priority: number;
  timeout?: number;
  debug: boolean;
  dry_run: boolean;
  variables: Record<string, any>;
}

export function NewBuildPage() {
  const navigate = useNavigate();
  const [currentStep, setCurrentStep] = useState(1);
  const [formData, setFormData] = useState<BuildFormData>({
    platform: '',
    location: '',
    spec: '',
    priority: 0,
    timeout: undefined,
    debug: false,
    dry_run: false,
    variables: {},
  });
  const [variablesText, setVariablesText] = useState('{}');
  const [validationErrors, setValidationErrors] = useState<string[]>([]);

  // Fetch configuration data
  const { data: platforms, isLoading: platformsLoading } = usePlatforms();
  const { data: locations, isLoading: locationsLoading } = useLocations();
  const { data: specs, isLoading: specsLoading } = useSpecs();

  // Mutations
  const createBuild = useCreateBuild();
  const validateSpec = useValidateSpec();

  const isLoading = platformsLoading || locationsLoading || specsLoading;

  // Filter available options based on selections
  const availableLocations = locations?.filter(location => {
    if (!formData.platform) return true;
    // Check if location supports the selected platform
    return location.config?.platforms?.includes(formData.platform) ?? true;
  }) || [];

  const availableSpecs = specs?.filter(spec => {
    // Basic filtering - can be enhanced based on platform/location compatibility
    return true;
  }) || [];

  // Validation
  useEffect(() => {
    const errors: string[] = [];

    if (currentStep >= 1) {
      if (!formData.platform) errors.push('Platform is required');
      if (!formData.location) errors.push('Location is required');
      if (!formData.spec) errors.push('Spec is required');
    }

    if (currentStep >= 2) {
      if (!isValidJSON(variablesText)) {
        errors.push('Variables must be valid JSON');
      }
      if (formData.timeout && (formData.timeout < 60 || formData.timeout > 86400)) {
        errors.push('Timeout must be between 60 seconds and 24 hours');
      }
    }

    setValidationErrors(errors);
  }, [formData, variablesText, currentStep]);

  const handleInputChange = (field: keyof BuildFormData, value: any) => {
    setFormData(prev => ({
      ...prev,
      [field]: value
    }));
  };

  const handleVariablesChange = (text: string) => {
    setVariablesText(text);
    if (isValidJSON(text)) {
      try {
        const parsed = JSON.parse(text);
        setFormData(prev => ({ ...prev, variables: parsed }));
      } catch {
        // Invalid JSON, keep previous variables
      }
    }
  };

  const handleNext = () => {
    if (validationErrors.length === 0) {
      setCurrentStep(prev => Math.min(prev + 1, 3));
    }
  };

  const handlePrevious = () => {
    setCurrentStep(prev => Math.max(prev - 1, 1));
  };

  const handleSubmit = async () => {
    if (validationErrors.length > 0) return;

    const buildConfig: BuildConfig = {
      platform: formData.platform,
      location: formData.location,
      spec: formData.spec,
      variables: formData.variables,
      timeout: formData.timeout,
      debug: formData.debug,
      dry_run: formData.dry_run,
    };

    try {
      const newBuild = await createBuild.mutateAsync({
        config: buildConfig,
        priority: formData.priority,
      });

      // Navigate to the new build's detail page
      navigate(`/builds/${newBuild.id}`);
    } catch (error) {
      // Error handled by the mutation
    }
  };

  if (isLoading) {
    return <LoadingState message="Loading configuration options..." />;
  }

  return (
    <div className="max-w-4xl mx-auto space-y-6">
      {/* Header */}
      <div className="flex items-center space-x-4">
        <Link
          to="/builds"
          className="p-2 hover:bg-gray-100 rounded-md transition-colors"
          title="Back to builds"
        >
          <ArrowLeft className="w-5 h-5" />
        </Link>
        <div>
          <h1 className="text-3xl font-bold text-gray-900">Create New Build</h1>
          <p className="text-gray-600 mt-1">
            Configure and start a new OS image build
          </p>
        </div>
      </div>

      {/* Progress steps */}
      <div className="card p-6">
        <div className="flex items-center justify-between mb-6">
          {[
            { step: 1, title: 'Configuration', icon: Settings },
            { step: 2, title: 'Options', icon: FileText },
            { step: 3, title: 'Review', icon: Eye },
          ].map(({ step, title, icon: Icon }) => (
            <div key={step} className="flex items-center">
              <div
                className={cn(
                  'flex items-center justify-center w-10 h-10 rounded-full border-2 transition-colors',
                  step <= currentStep
                    ? 'bg-primary-600 border-primary-600 text-white'
                    : 'border-gray-300 text-gray-400'
                )}
              >
                {step < currentStep ? (
                  <CheckCircle className="w-5 h-5" />
                ) : (
                  <Icon className="w-5 h-5" />
                )}
              </div>
              <span
                className={cn(
                  'ml-3 text-sm font-medium',
                  step <= currentStep ? 'text-gray-900' : 'text-gray-400'
                )}
              >
                {title}
              </span>
              {step < 3 && (
                <div
                  className={cn(
                    'ml-8 w-16 h-0.5 transition-colors',
                    step < currentStep ? 'bg-primary-600' : 'bg-gray-300'
                  )}
                />
              )}
            </div>
          ))}
        </div>

        {/* Step content */}
        <div className="min-h-80">
          {currentStep === 1 && (
            <ConfigurationStep
              formData={formData}
              platforms={platforms || []}
              locations={availableLocations}
              specs={availableSpecs}
              onChange={handleInputChange}
            />
          )}

          {currentStep === 2 && (
            <OptionsStep
              formData={formData}
              variablesText={variablesText}
              onChange={handleInputChange}
              onVariablesChange={handleVariablesChange}
            />
          )}

          {currentStep === 3 && (
            <ReviewStep
              formData={formData}
              platforms={platforms || []}
              locations={locations || []}
              specs={specs || []}
            />
          )}
        </div>

        {/* Validation errors */}
        {validationErrors.length > 0 && (
          <div className="mb-6 p-4 bg-danger-50 border border-danger-200 rounded-lg">
            <div className="flex items-start space-x-3">
              <AlertCircle className="w-5 h-5 text-danger-600 mt-0.5" />
              <div>
                <h3 className="text-sm font-medium text-danger-800">Please fix the following errors:</h3>
                <ul className="mt-2 text-sm text-danger-700">
                  {validationErrors.map((error, index) => (
                    <li key={index}> {error}</li>
                  ))}
                </ul>
              </div>
            </div>
          </div>
        )}

        {/* Navigation buttons */}
        <div className="flex justify-between pt-6 border-t border-gray-200">
          <button
            onClick={handlePrevious}
            disabled={currentStep === 1}
            className="btn-secondary disabled:opacity-50 disabled:cursor-not-allowed"
          >
            Previous
          </button>

          <div className="flex space-x-3">
            {currentStep < 3 ? (
              <button
                onClick={handleNext}
                disabled={validationErrors.length > 0}
                className="btn-primary disabled:opacity-50 disabled:cursor-not-allowed"
              >
                Next
              </button>
            ) : (
              <LoadingButton
                loading={createBuild.isPending}
                onClick={handleSubmit}
                disabled={validationErrors.length > 0}
                className="btn-primary"
              >
                <Play className="w-4 h-4 mr-2" />
                {formData.dry_run ? 'Start Dry Run' : 'Start Build'}
              </LoadingButton>
            )}
          </div>
        </div>
      </div>
    </div>
  );
}

// Configuration step component
interface ConfigurationStepProps {
  formData: BuildFormData;
  platforms: Platform[];
  locations: Location[];
  specs: Spec[];
  onChange: (field: keyof BuildFormData, value: any) => void;
}

function ConfigurationStep({ formData, platforms, locations, specs, onChange }: ConfigurationStepProps) {
  return (
    <div className="space-y-6">
      <div>
        <h2 className="text-xl font-semibold text-gray-900 mb-4">Build Configuration</h2>
        <p className="text-gray-600 mb-6">
          Select the platform, location, and specification for your build.
        </p>
      </div>

      <div className="grid grid-cols-1 md:grid-cols-3 gap-6">
        {/* Platform selection */}
        <div>
          <label className="label">
            <Layers className="w-4 h-4 mr-2" />
            Platform
          </label>
          <select
            className="input"
            value={formData.platform}
            onChange={(e) => onChange('platform', e.target.value)}
          >
            <option value="">Select a platform...</option>
            {platforms.map((platform) => (
              <option key={platform.name} value={platform.name}>
                {platform.name} ({platform.type})
              </option>
            ))}
          </select>
          {formData.platform && (
            <p className="text-xs text-gray-500 mt-1">
              {platforms.find(p => p.name === formData.platform)?.description || 'No description'}
            </p>
          )}
        </div>

        {/* Location selection */}
        <div>
          <label className="label">
            <MapPin className="w-4 h-4 mr-2" />
            Location
          </label>
          <select
            className="input"
            value={formData.location}
            onChange={(e) => onChange('location', e.target.value)}
            disabled={!formData.platform}
          >
            <option value="">Select a location...</option>
            {locations.map((location) => (
              <option key={location.name} value={location.name}>
                {location.name}
              </option>
            ))}
          </select>
          {formData.location && (
            <p className="text-xs text-gray-500 mt-1">
              {locations.find(l => l.name === formData.location)?.description || 'No description'}
            </p>
          )}
        </div>

        {/* Spec selection */}
        <div>
          <label className="label">
            <FileText className="w-4 h-4 mr-2" />
            Specification
          </label>
          <select
            className="input"
            value={formData.spec}
            onChange={(e) => onChange('spec', e.target.value)}
          >
            <option value="">Select a spec...</option>
            {specs.map((spec) => (
              <option key={spec.name} value={spec.name}>
                {spec.name} ({spec.os_type})
              </option>
            ))}
          </select>
          {formData.spec && (
            <p className="text-xs text-gray-500 mt-1">
              {specs.find(s => s.name === formData.spec)?.description || 'No description'}
            </p>
          )}
        </div>
      </div>

      {/* Selected configuration preview */}
      {formData.platform && formData.location && formData.spec && (
        <div className="bg-success-50 border border-success-200 rounded-lg p-4">
          <div className="flex items-start space-x-3">
            <CheckCircle className="w-5 h-5 text-success-600 mt-0.5" />
            <div>
              <h3 className="text-sm font-medium text-success-800">Configuration Valid</h3>
              <p className="text-sm text-success-700 mt-1">
                Ready to build <strong>{formData.spec}</strong> on <strong>{formData.platform}</strong> in <strong>{formData.location}</strong>
              </p>
            </div>
          </div>
        </div>
      )}
    </div>
  );
}

// Options step component
interface OptionsStepProps {
  formData: BuildFormData;
  variablesText: string;
  onChange: (field: keyof BuildFormData, value: any) => void;
  onVariablesChange: (text: string) => void;
}

function OptionsStep({ formData, variablesText, onChange, onVariablesChange }: OptionsStepProps) {
  return (
    <div className="space-y-6">
      <div>
        <h2 className="text-xl font-semibold text-gray-900 mb-4">Build Options</h2>
        <p className="text-gray-600 mb-6">
          Configure advanced options and custom variables for your build.
        </p>
      </div>

      <div className="grid grid-cols-1 lg:grid-cols-2 gap-6">
        {/* Basic options */}
        <div className="space-y-4">
          <h3 className="text-lg font-medium text-gray-900">Basic Options</h3>

          {/* Priority */}
          <div>
            <label className="label">Build Priority</label>
            <select
              className="input"
              value={formData.priority}
              onChange={(e) => onChange('priority', parseInt(e.target.value))}
            >
              <option value={0}>Normal (0)</option>
              <option value={1}>Low (1)</option>
              <option value={5}>High (5)</option>
              <option value={10}>Critical (10)</option>
            </select>
            <p className="text-xs text-gray-500 mt-1">
              Higher priority builds will be processed first
            </p>
          </div>

          {/* Timeout */}
          <div>
            <label className="label">Timeout (seconds)</label>
            <input
              type="number"
              className="input"
              value={formData.timeout || ''}
              onChange={(e) => onChange('timeout', e.target.value ? parseInt(e.target.value) : undefined)}
              placeholder="Default timeout"
              min="60"
              max="86400"
            />
            <p className="text-xs text-gray-500 mt-1">
              Maximum time allowed for the build (60 seconds to 24 hours)
            </p>
          </div>

          {/* Checkboxes */}
          <div className="space-y-3">
            <label className="flex items-center space-x-3">
              <input
                type="checkbox"
                checked={formData.debug}
                onChange={(e) => onChange('debug', e.target.checked)}
                className="rounded border-gray-300 text-primary-600 focus:ring-primary-500"
              />
              <span className="text-sm font-medium text-gray-700">Enable debug mode</span>
            </label>

            <label className="flex items-center space-x-3">
              <input
                type="checkbox"
                checked={formData.dry_run}
                onChange={(e) => onChange('dry_run', e.target.checked)}
                className="rounded border-gray-300 text-primary-600 focus:ring-primary-500"
              />
              <span className="text-sm font-medium text-gray-700">Dry run (validate without building)</span>
            </label>
          </div>
        </div>

        {/* Custom variables */}
        <div className="space-y-4">
          <h3 className="text-lg font-medium text-gray-900">Custom Variables</h3>
          
          <div>
            <label className="label">Variables (JSON)</label>
            <textarea
              className={cn(
                'input h-48 font-mono text-sm',
                !isValidJSON(variablesText) && 'border-danger-300 focus:border-danger-500 focus:ring-danger-500'
              )}
              value={variablesText}
              onChange={(e) => onVariablesChange(e.target.value)}
              placeholder='{\n  "hostname": "myserver",\n  "domain": "example.com"\n}'
            />
            {!isValidJSON(variablesText) && (
              <p className="text-xs text-danger-600 mt-1">
                Invalid JSON format
              </p>
            )}
            <p className="text-xs text-gray-500 mt-1">
              Custom variables to override default values during the build
            </p>
          </div>

          {/* Common variables suggestions */}
          <div className="bg-info-50 border border-info-200 rounded-lg p-4">
            <div className="flex items-start space-x-3">
              <Info className="w-5 h-5 text-info-600 mt-0.5" />
              <div>
                <h4 className="text-sm font-medium text-info-800">Common Variables</h4>
                <ul className="text-sm text-info-700 mt-1 space-y-1">
                  <li> <code>hostname</code>: Set the system hostname</li>
                  <li> <code>domain</code>: Set the domain name</li>
                  <li> <code>root_password</code>: Set root password</li>
                  <li> <code>packages</code>: Additional packages to install</li>
                </ul>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}

// Review step component
interface ReviewStepProps {
  formData: BuildFormData;
  platforms: Platform[];
  locations: Location[];
  specs: Spec[];
}

function ReviewStep({ formData, platforms, locations, specs }: ReviewStepProps) {
  const platform = platforms.find(p => p.name === formData.platform);
  const location = locations.find(l => l.name === formData.location);
  const spec = specs.find(s => s.name === formData.spec);

  return (
    <div className="space-y-6">
      <div>
        <h2 className="text-xl font-semibold text-gray-900 mb-4">Review Build Configuration</h2>
        <p className="text-gray-600 mb-6">
          Please review your build configuration before starting the build.
        </p>
      </div>

      <div className="grid grid-cols-1 lg:grid-cols-2 gap-6">
        {/* Basic configuration */}
        <div className="space-y-4">
          <h3 className="text-lg font-medium text-gray-900">Configuration</h3>
          
          <div className="space-y-3">
            <div className="flex justify-between items-center py-2 border-b border-gray-100">
              <span className="text-sm font-medium text-gray-600">Platform</span>
              <span className="text-sm text-gray-900">{platform?.name} ({platform?.type})</span>
            </div>
            <div className="flex justify-between items-center py-2 border-b border-gray-100">
              <span className="text-sm font-medium text-gray-600">Location</span>
              <span className="text-sm text-gray-900">{location?.name}</span>
            </div>
            <div className="flex justify-between items-center py-2 border-b border-gray-100">
              <span className="text-sm font-medium text-gray-600">Specification</span>
              <span className="text-sm text-gray-900">{spec?.name} ({spec?.os_type})</span>
            </div>
            <div className="flex justify-between items-center py-2 border-b border-gray-100">
              <span className="text-sm font-medium text-gray-600">Priority</span>
              <span className="text-sm text-gray-900">
                {formData.priority === 0 ? 'Normal' : 
                 formData.priority === 1 ? 'Low' :
                 formData.priority === 5 ? 'High' : 'Critical'} ({formData.priority})
              </span>
            </div>
            {formData.timeout && (
              <div className="flex justify-between items-center py-2 border-b border-gray-100">
                <span className="text-sm font-medium text-gray-600">Timeout</span>
                <span className="text-sm text-gray-900">{formData.timeout} seconds</span>
              </div>
            )}
            <div className="flex justify-between items-center py-2 border-b border-gray-100">
              <span className="text-sm font-medium text-gray-600">Debug Mode</span>
              <span className="text-sm text-gray-900">{formData.debug ? 'Enabled' : 'Disabled'}</span>
            </div>
            <div className="flex justify-between items-center py-2">
              <span className="text-sm font-medium text-gray-600">Dry Run</span>
              <span className="text-sm text-gray-900">{formData.dry_run ? 'Yes' : 'No'}</span>
            </div>
          </div>
        </div>

        {/* Custom variables */}
        <div className="space-y-4">
          <h3 className="text-lg font-medium text-gray-900">Custom Variables</h3>
          
          {Object.keys(formData.variables).length === 0 ? (
            <p className="text-gray-500 text-sm">No custom variables defined</p>
          ) : (
            <div className="bg-gray-50 rounded-lg p-4">
              <pre className="text-sm text-gray-900 overflow-x-auto">
                {JSON.stringify(formData.variables, null, 2)}
              </pre>
            </div>
          )}
        </div>
      </div>

      {/* Final confirmation */}
      <div className="bg-primary-50 border border-primary-200 rounded-lg p-4">
        <div className="flex items-start space-x-3">
          <Info className="w-5 h-5 text-primary-600 mt-0.5" />
          <div>
            <h3 className="text-sm font-medium text-primary-800">Ready to Start</h3>
            <p className="text-sm text-primary-700 mt-1">
              {formData.dry_run 
                ? 'This will validate your configuration without creating an actual image.'
                : 'This will start building your OS image. The process may take several minutes to hours depending on the configuration.'
              }
            </p>
          </div>
        </div>
      </div>
    </div>
  );
}
