/**
 * Dashboard page component showing system overview and active builds.
 */

import { Link } from 'react-router-dom';
import { 
  Activity, 
  CheckCircle, 
  XCircle, 
  Clock, 
  Plus,
  BarChart3,
  Zap,
  Server
} from 'lucide-react';
import { useBuilds, useSystemInfo, useStatus } from '@/hooks/api-hooks';
import { useAppStore } from '@/stores/app-store';
import { LoadingState } from '@/components/ui/loading';
import { StatusBadge } from '@/components/ui/status-badge';
import { ProgressBar, CircularProgress } from '@/components/ui/progress-bar';
import { formatRelativeTime, formatDuration, isBuildActive } from '@/lib/utils';
import type { Build } from '@/types/api';

export function Dashboard() {
  const { data: builds, isLoading: buildsLoading } = useBuilds(undefined, 10);
  const { data: systemInfo } = useSystemInfo();
  const { data: status } = useStatus();
  const { activeBuildCount } = useAppStore();

  if (buildsLoading) {
    return <LoadingState message="Loading dashboard..." />;
  }

  const recentBuilds = builds?.builds.slice(0, 5) || [];
  const activeBuilds = recentBuilds.filter(build => isBuildActive(build.status));
  const completedBuilds = recentBuilds.filter(build => build.status === 'completed');
  const failedBuilds = recentBuilds.filter(build => build.status === 'failed');

  return (
    <div className="space-y-8">
      {/* Page header */}
      <div className="flex justify-between items-center">
        <div>
          <h1 className="text-3xl font-bold text-gray-900">Dashboard</h1>
          <p className="text-gray-600 mt-1">
            Overview of your OSImager system and recent builds
          </p>
        </div>
        <Link
          to="/builds/new"
          className="btn-primary"
        >
          <Plus className="w-4 h-4 mr-2" />
          New Build
        </Link>
      </div>

      {/* Stats cards */}
      <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-6">
        <StatsCard
          title="Active Builds"
          value={activeBuildCount}
          icon={Activity}
          color="primary"
          description="Currently running"
        />
        <StatsCard
          title="Completed Today"
          value={completedBuilds.length}
          icon={CheckCircle}
          color="success"
          description="Successfully finished"
        />
        <StatsCard
          title="Failed Today"
          value={failedBuilds.length}
          icon={XCircle}
          color="danger"
          description="Build failures"
        />
        <StatsCard
          title="Total Builds"
          value={builds?.total || 0}
          icon={BarChart3}
          color="info"
          description="All time"
        />
      </div>

      {/* Main content grid */}
      <div className="grid grid-cols-1 lg:grid-cols-3 gap-8">
        {/* Active builds */}
        <div className="lg:col-span-2">
          <div className="card p-6">
            <div className="flex items-center justify-between mb-6">
              <h2 className="text-lg font-semibold text-gray-900">Active Builds</h2>
              <Link to="/builds" className="text-primary-600 hover:text-primary-700 text-sm font-medium">
                View all 
              </Link>
            </div>
            
            {activeBuilds.length === 0 ? (
              <div className="text-center py-8 text-gray-500">
                <Activity className="w-12 h-12 mx-auto mb-4 text-gray-300" />
                <p>No active builds</p>
                <Link to="/builds/new" className="text-primary-600 hover:text-primary-700 text-sm mt-2 inline-block">
                  Start a new build
                </Link>
              </div>
            ) : (
              <div className="space-y-4">
                {activeBuilds.map((build) => (
                  <ActiveBuildCard key={build.id} build={build} />
                ))}
              </div>
            )}
          </div>
        </div>

        {/* System info and recent builds */}
        <div className="space-y-6">
          {/* System status */}
          <div className="card p-6">
            <h2 className="text-lg font-semibold text-gray-900 mb-4">System Status</h2>
            <div className="space-y-3">
              <div className="flex justify-between items-center">
                <span className="text-gray-600">Build Manager</span>
                <StatusBadge status="running" type="custom" />
              </div>
              <div className="flex justify-between items-center">
                <span className="text-gray-600">API Version</span>
                <span className="text-sm font-mono">{systemInfo?.osimager_version || 'Unknown'}</span>
              </div>
              <div className="flex justify-between items-center">
                <span className="text-gray-600">Base Directory</span>
                <span className="text-xs text-gray-500 truncate max-w-32" title={systemInfo?.base_directory}>
                  {systemInfo?.base_directory || 'N/A'}
                </span>
              </div>
            </div>
          </div>

          {/* Recent builds */}
          <div className="card p-6">
            <div className="flex items-center justify-between mb-4">
              <h2 className="text-lg font-semibold text-gray-900">Recent Builds</h2>
              <Link to="/builds" className="text-primary-600 hover:text-primary-700 text-sm font-medium">
                View all 
              </Link>
            </div>
            
            {recentBuilds.length === 0 ? (
              <p className="text-gray-500 text-center py-4">No recent builds</p>
            ) : (
              <div className="space-y-3">
                {recentBuilds.map((build) => (
                  <RecentBuildItem key={build.id} build={build} />
                ))}
              </div>
            )}
          </div>
        </div>
      </div>
    </div>
  );
}

// Stats card component
interface StatsCardProps {
  title: string;
  value: number;
  icon: React.ComponentType<{ className?: string }>;
  color: 'primary' | 'success' | 'danger' | 'info';
  description: string;
}

function StatsCard({ title, value, icon: Icon, color, description }: StatsCardProps) {
  const colorClasses = {
    primary: 'bg-primary-100 text-primary-600',
    success: 'bg-success-100 text-success-600',
    danger: 'bg-danger-100 text-danger-600',
    info: 'bg-gray-100 text-gray-600',
  };

  return (
    <div className="card p-6">
      <div className="flex items-center">
        <div className={`p-3 rounded-lg ${colorClasses[color]}`}>
          <Icon className="w-6 h-6" />
        </div>
        <div className="ml-4">
          <p className="text-sm font-medium text-gray-600">{title}</p>
          <p className="text-2xl font-bold text-gray-900">{value}</p>
          <p className="text-xs text-gray-500">{description}</p>
        </div>
      </div>
    </div>
  );
}

// Active build card component
function ActiveBuildCard({ build }: { build: Build }) {
  const { config, status, progress, started_at } = build;
  
  return (
    <Link
      to={`/builds/${build.id}`}
      className="block p-4 border border-gray-200 rounded-lg hover:border-primary-300 hover:shadow-md transition-all"
    >
      <div className="flex items-center justify-between mb-3">
        <div className="flex items-center space-x-3">
          <StatusBadge status={status} />
          <span className="text-sm font-medium text-gray-900">
            {config.platform}  {config.spec}
          </span>
        </div>
        <span className="text-xs text-gray-500">
          {formatRelativeTime(started_at)}
        </span>
      </div>
      
      {progress && (
        <ProgressBar progress={progress} showDetails={false} />
      )}
    </Link>
  );
}

// Recent build item component
function RecentBuildItem({ build }: { build: Build }) {
  const { config, status, started_at, duration } = build;
  
  return (
    <Link
      to={`/builds/${build.id}`}
      className="flex items-center justify-between p-3 hover:bg-gray-50 rounded-lg transition-colors"
    >
      <div className="flex items-center space-x-3">
        <StatusBadge status={status} showIcon={false} />
        <div>
          <p className="text-sm font-medium text-gray-900">
            {config.platform}  {config.spec}
          </p>
          <p className="text-xs text-gray-500">
            {formatRelativeTime(started_at)}
          </p>
        </div>
      </div>
      
      {duration && (
        <span className="text-xs text-gray-500">
          {formatDuration(duration)}
        </span>
      )}
    </Link>
  );
}
