/**
 * React hook for WebSocket connection and real-time updates.
 */

import { useEffect, useRef, useCallback } from 'react';
import { useQueryClient } from '@tanstack/react-query';
import { toast } from 'react-hot-toast';
import { useAppStore } from '@/stores/app-store';
import { 
  wsClient, 
  createInitialStatusHandler,
  createBuildStatusHandler,
  createBuildLogHandler,
  createBuildProgressHandler,
} from '@/lib/websocket-client';
import { queryKeys } from './api-hooks';
import type { WebSocketMessage } from '@/types/api';

export function useWebSocket() {
  const queryClient = useQueryClient();
  const connectedRef = useRef(false);
  
  const {
    isConnected,
    setConnected,
    realtimeEnabled,
    updateBuild,
    addNotification,
    updateLastUpdate,
  } = useAppStore();

  // Connection status handler
  const handleConnection = useCallback((message: WebSocketMessage) => {
    if (message.type === 'connection') {
      const connected = message.data?.connected || false;
      setConnected(connected);
      connectedRef.current = connected;
      
      if (connected) {
        addNotification({
          type: 'success',
          title: 'Connected',
          message: 'Real-time updates enabled',
        });
      } else {
        addNotification({
          type: 'warning',
          title: 'Disconnected',
          message: 'Real-time updates disabled',
        });
      }
    }
  }, [setConnected, addNotification]);

  // Initial status handler
  const handleInitialStatus = useCallback(createInitialStatusHandler((data) => {
    console.log('Received initial status:', data);
    updateLastUpdate();
    
    // Invalidate builds query to refetch with latest data
    queryClient.invalidateQueries({ queryKey: queryKeys.builds });
  }), [updateLastUpdate, queryClient]);

  // Build status handler
  const handleBuildStatus = useCallback(createBuildStatusHandler((buildId, build) => {
    console.log('Build status update:', buildId, build.status);
    updateBuild(buildId, build);
    updateLastUpdate();
    
    // Invalidate specific build query
    queryClient.setQueryData(queryKeys.build(buildId), build);
    
    // Show notification for status changes
    if (build.status === 'completed') {
      addNotification({
        type: 'success',
        title: 'Build Completed',
        message: `Build ${buildId.substring(0, 8)} completed successfully`,
      });
    } else if (build.status === 'failed') {
      addNotification({
        type: 'error',
        title: 'Build Failed',
        message: `Build ${buildId.substring(0, 8)} failed: ${build.error_message || 'Unknown error'}`,
      });
    }
  }), [updateBuild, updateLastUpdate, queryClient, addNotification]);

  // Build log handler
  const handleBuildLog = useCallback(createBuildLogHandler((buildId, logEntry) => {
    console.debug('Build log:', buildId, logEntry.level, logEntry.message);
    updateLastUpdate();
    
    // Update build logs cache
    queryClient.setQueryData(
      queryKeys.buildLogs(buildId),
      (oldLogs: any[]) => oldLogs ? [...oldLogs, logEntry] : [logEntry]
    );
    
    // Show toast for error logs
    if (logEntry.level === 'error' || logEntry.level === 'critical') {
      toast.error(`Build ${buildId.substring(0, 8)}: ${logEntry.message}`);
    }
  }), [updateLastUpdate, queryClient]);

  // Build progress handler
  const handleBuildProgress = useCallback(createBuildProgressHandler((buildId, progress) => {
    console.debug('Build progress:', buildId, progress.percentage);
    updateBuild(buildId, { progress });
    updateLastUpdate();
  }), [updateBuild, updateLastUpdate]);

  // Error handler
  const handleError = useCallback((message: WebSocketMessage) => {
    if (message.type === 'error') {
      console.error('WebSocket error:', message.data);
      addNotification({
        type: 'error',
        title: 'WebSocket Error',
        message: message.data?.error || 'Unknown WebSocket error',
      });
    }
  }, [addNotification]);

  // Pong handler (heartbeat response)
  const handlePong = useCallback((message: WebSocketMessage) => {
    if (message.type === 'pong') {
      console.debug('WebSocket heartbeat pong received');
    }
  }, []);

  // Setup WebSocket connection
  useEffect(() => {
    let mounted = true;
    
    if (!realtimeEnabled) {
      wsClient.disconnect();
      setConnected(false);
      return;
    }

    // Register event handlers
    wsClient.onMessage(handleConnection);
    wsClient.on('initial_status', handleInitialStatus);
    wsClient.on('build_status', handleBuildStatus);
    wsClient.on('build_log', handleBuildLog);
    wsClient.on('build_progress', handleBuildProgress);
    wsClient.on('error', handleError);
    wsClient.on('pong', handlePong);

    // Connect only if component is still mounted
    if (mounted) {
      wsClient.connect().catch(error => {
        console.error('Failed to connect WebSocket:', error);
        if (mounted) {
          addNotification({
            type: 'error',
            title: 'Connection Failed',
            message: 'Failed to establish real-time connection',
          });
        }
      });
    }

    // Cleanup on unmount
    return () => {
      mounted = false;
      wsClient.offMessage(handleConnection);
      wsClient.off('initial_status', handleInitialStatus);
      wsClient.off('build_status', handleBuildStatus);
      wsClient.off('build_log', handleBuildLog);
      wsClient.off('build_progress', handleBuildProgress);
      wsClient.off('error', handleError);
      wsClient.off('pong', handlePong);
      
      // Only disconnect if this is a real unmount (not React StrictMode)
      setTimeout(() => {
        if (!mounted) {
          wsClient.disconnect();
        }
      }, 100);
    };
  }, [
    realtimeEnabled,
    handleConnection,
    handleInitialStatus,
    handleBuildStatus,
    handleBuildLog,
    handleBuildProgress,
    handleError,
    handlePong,
    setConnected,
    addNotification,
  ]);

  // Subscribe to specific build
  const subscribeToBuild = useCallback((buildId: string) => {
    if (isConnected) {
      wsClient.subscribeToBuild(buildId);
    }
  }, [isConnected]);

  // Send ping (manual heartbeat)
  const sendPing = useCallback(() => {
    if (isConnected) {
      wsClient.send({ type: 'ping' });
    }
  }, [isConnected]);

  // Manual reconnect
  const reconnect = useCallback(() => {
    wsClient.reconnect();
  }, []);

  return {
    isConnected,
    subscribeToBuild,
    sendPing,
    reconnect,
  };
}

// Hook for automatically subscribing to a build
export function useBuildSubscription(buildId: string | null) {
  const { subscribeToBuild, isConnected } = useWebSocket();
  
  useEffect(() => {
    if (buildId && isConnected) {
      subscribeToBuild(buildId);
    }
  }, [buildId, isConnected, subscribeToBuild]);
}
