/**
 * Header component with navigation and status indicators.
 */

import { useState } from 'react';
import { Link, useLocation } from 'react-router-dom';
import { 
  Menu, 
  X, 
  Settings, 
  Activity, 
  Database, 
  Layers, 
  MapPin,
  FileText,
  Monitor,
  Bell
} from 'lucide-react';
import { cn } from '@/lib/utils';
import { useAppStore } from '@/stores/app-store';
import { useHealth } from '@/hooks/api-hooks';
import { useWebSocket } from '@/hooks/websocket-hook';
import { ConnectionStatus, HealthStatus } from '@/components/ui/status-badge';
import { LoadingSpinner } from '@/components/ui/loading';

const navigation = [
  { name: 'Dashboard', href: '/', icon: Monitor },
  { name: 'Builds', href: '/builds', icon: Activity },
  { name: 'Specs', href: '/specs', icon: FileText },
  { name: 'Platforms', href: '/platforms', icon: Layers },
  { name: 'Locations', href: '/locations', icon: MapPin },
];

export function Header() {
  const [mobileMenuOpen, setMobileMenuOpen] = useState(false);
  const location = useLocation();
  
  const { isConnected, activeBuildCount, notifications } = useAppStore();
  const { data: health, isLoading: healthLoading } = useHealth();
  const { reconnect } = useWebSocket();
  
  const unreadNotifications = notifications.filter(n => !n.dismissed).length;

  return (
    <header className="bg-white shadow border-b border-gray-200">
      <div className="mx-auto max-w-7xl px-4 sm:px-6 lg:px-8">
        <div className="flex justify-between items-center py-4">
          {/* Logo and title */}
          <div className="flex items-center">
            <Link to="/" className="flex items-center space-x-3">
              <div className="w-8 h-8 bg-primary-600 rounded-lg flex items-center justify-center">
                <Database className="w-5 h-5 text-white" />
              </div>
              <div>
                <h1 className="text-xl font-bold text-gray-900">OSImager</h1>
                <p className="text-xs text-gray-500">Image Builder</p>
              </div>
            </Link>
          </div>

          {/* Desktop Navigation */}
          <nav className="hidden md:flex space-x-1">
            {navigation.map((item) => {
              const Icon = item.icon;
              const isActive = location.pathname === item.href;
              
              return (
                <Link
                  key={item.name}
                  to={item.href}
                  className={cn(
                    'flex items-center space-x-2 px-3 py-2 rounded-md text-sm font-medium transition-colors',
                    isActive
                      ? 'bg-primary-100 text-primary-700'
                      : 'text-gray-600 hover:text-gray-900 hover:bg-gray-100'
                  )}
                >
                  <Icon className="w-4 h-4" />
                  <span>{item.name}</span>
                </Link>
              );
            })}
          </nav>

          {/* Status indicators and actions */}
          <div className="flex items-center space-x-4">
            {/* Active builds indicator */}
            {activeBuildCount > 0 && (
              <Link
                to="/builds"
                className="flex items-center space-x-2 px-3 py-1.5 bg-primary-100 text-primary-700 rounded-full text-sm font-medium hover:bg-primary-200 transition-colors"
              >
                <Activity className="w-4 h-4" />
                <span>{activeBuildCount} active</span>
              </Link>
            )}

            {/* Notifications */}
            <button
              className="relative p-2 text-gray-600 hover:text-gray-900 hover:bg-gray-100 rounded-md transition-colors"
              title="Notifications"
            >
              <Bell className="w-5 h-5" />
              {unreadNotifications > 0 && (
                <span className="absolute -top-1 -right-1 w-5 h-5 bg-danger-500 text-white text-xs rounded-full flex items-center justify-center">
                  {unreadNotifications > 9 ? '9+' : unreadNotifications}
                </span>
              )}
            </button>

            {/* Connection status */}
            <ConnectionStatus connected={isConnected} onReconnect={reconnect} />

            {/* Health status */}
            <div className="flex items-center space-x-2">
              {healthLoading ? (
                <LoadingSpinner size="sm" />
              ) : health ? (
                <HealthStatus status={health.status} />
              ) : (
                <HealthStatus status="unknown" />
              )}
            </div>

            {/* Settings */}
            <Link
              to="/settings"
              className="p-2 text-gray-600 hover:text-gray-900 hover:bg-gray-100 rounded-md transition-colors"
              title="Settings"
            >
              <Settings className="w-5 h-5" />
            </Link>

            {/* Mobile menu button */}
            <button
              type="button"
              className="md:hidden p-2 text-gray-600 hover:text-gray-900 hover:bg-gray-100 rounded-md transition-colors"
              onClick={() => setMobileMenuOpen(!mobileMenuOpen)}
            >
              {mobileMenuOpen ? (
                <X className="w-6 h-6" />
              ) : (
                <Menu className="w-6 h-6" />
              )}
            </button>
          </div>
        </div>

        {/* Mobile Navigation */}
        {mobileMenuOpen && (
          <div className="md:hidden py-4 border-t border-gray-200">
            <nav className="space-y-1">
              {navigation.map((item) => {
                const Icon = item.icon;
                const isActive = location.pathname === item.href;
                
                return (
                  <Link
                    key={item.name}
                    to={item.href}
                    onClick={() => setMobileMenuOpen(false)}
                    className={cn(
                      'flex items-center space-x-3 px-3 py-2 rounded-md text-base font-medium transition-colors',
                      isActive
                        ? 'bg-primary-100 text-primary-700'
                        : 'text-gray-600 hover:text-gray-900 hover:bg-gray-100'
                    )}
                  >
                    <Icon className="w-5 h-5" />
                    <span>{item.name}</span>
                  </Link>
                );
              })}
            </nav>
          </div>
        )}
      </div>
    </header>
  );
}
