/**
 * Build Details page component for monitoring individual builds.
 */

import { useState, useEffect } from 'react';
import { useParams, Link, useNavigate } from 'react-router-dom';
import {
  ArrowLeft,
  Download,
  Square,
  RefreshCw,
  Copy,
  FileText,
  Clock,
  Calendar,
  Settings,
  Terminal,
  AlertCircle,
  CheckCircle,
  XCircle,
  Pause
} from 'lucide-react';
import { useBuild, useBuildLogs, useCancelBuild } from '@/hooks/api-hooks';
import { useBuildSubscription } from '@/hooks/websocket-hook';
import { LoadingState, LoadingButton } from '@/components/ui/loading';
import { StatusBadge } from '@/components/ui/status-badge';
import { ProgressBar, CircularProgress } from '@/components/ui/progress-bar';
import {
  formatDate,
  formatRelativeTime,
  formatDuration,
  formatFileSize,
  isBuildActive,
  copyToClipboard,
  downloadAsFile,
  cn
} from '@/lib/utils';
import type { Build, BuildLogEntry } from '@/types/api';

export function BuildDetailsPage() {
  const { buildId } = useParams<{ buildId: string }>();
  const navigate = useNavigate();
  const [activeTab, setActiveTab] = useState<'overview' | 'logs' | 'config' | 'artifacts'>('overview');
  const [logSearch, setLogSearch] = useState('');
  const [logLevel, setLogLevel] = useState<'all' | 'error' | 'warning' | 'info'>('all');

  // Subscribe to real-time updates for this build
  useBuildSubscription(buildId || null);

  // Fetch build data
  const { data: build, isLoading, error } = useBuild(buildId || null);
  const { data: logs } = useBuildLogs(buildId || null, 1000);
  const cancelBuild = useCancelBuild();

  if (isLoading) {
    return <LoadingState message="Loading build details..." />;
  }

  if (error || !build) {
    return (
      <div className="text-center py-12">
        <XCircle className="w-16 h-16 text-danger-500 mx-auto mb-4" />
        <h2 className="text-xl font-semibold text-gray-900 mb-2">Build Not Found</h2>
        <p className="text-gray-600 mb-6">
          The build you're looking for doesn't exist or you don't have permission to view it.
        </p>
        <Link to="/builds" className="btn-primary">
          <ArrowLeft className="w-4 h-4 mr-2" />
          Back to Builds
        </Link>
      </div>
    );
  }

  const handleCancelBuild = async () => {
    if (window.confirm('Are you sure you want to cancel this build?')) {
      try {
        await cancelBuild.mutateAsync(build.id);
      } catch (error) {
        // Error handled by mutation
      }
    }
  };

  const handleCopyId = async () => {
    const success = await copyToClipboard(build.id);
    if (success) {
      // Toast will be shown by the mutation
    }
  };

  const filteredLogs = logs?.filter(log => {
    const matchesSearch = !logSearch || 
      log.message.toLowerCase().includes(logSearch.toLowerCase()) ||
      log.source.toLowerCase().includes(logSearch.toLowerCase());
    
    const matchesLevel = logLevel === 'all' || log.level === logLevel;
    
    return matchesSearch && matchesLevel;
  }) || [];

  return (
    <div className="space-y-6">
      {/* Header */}
      <div className="flex items-center justify-between">
        <div className="flex items-center space-x-4">
          <Link
            to="/builds"
            className="p-2 hover:bg-gray-100 rounded-md transition-colors"
            title="Back to builds"
          >
            <ArrowLeft className="w-5 h-5" />
          </Link>
          <div>
            <h1 className="text-3xl font-bold text-gray-900">Build Details</h1>
            <div className="flex items-center space-x-3 mt-1">
              <StatusBadge status={build.status} />
              <span className="text-gray-500"></span>
              <button
                onClick={handleCopyId}
                className="flex items-center space-x-1 text-gray-600 hover:text-gray-900 transition-colors"
                title="Copy build ID"
              >
                <span className="text-sm font-mono">{build.id.substring(0, 8)}</span>
                <Copy className="w-3 h-3" />
              </button>
            </div>
          </div>
        </div>

        <div className="flex items-center space-x-3">
          {isBuildActive(build.status) && (
            <LoadingButton
              loading={cancelBuild.isPending}
              onClick={handleCancelBuild}
              className="btn-danger"
            >
              <Square className="w-4 h-4 mr-2" />
              Cancel Build
            </LoadingButton>
          )}
          <Link to={`/builds/${build.id}/clone`} className="btn-secondary">
            <Copy className="w-4 h-4 mr-2" />
            Clone Build
          </Link>
        </div>
      </div>

      {/* Quick stats */}
      <div className="grid grid-cols-1 md:grid-cols-4 gap-4">
        <QuickStat
          icon={Settings}
          label="Platform"
          value={build.config.platform}
          description={build.config.location}
        />
        <QuickStat
          icon={FileText}
          label="Spec"
          value={build.config.spec}
          description="OS specification"
        />
        <QuickStat
          icon={Calendar}
          label="Started"
          value={build.started_at ? formatRelativeTime(build.started_at) : 'Not started'}
          description={build.started_at ? formatDate(build.started_at) : ''}
        />
        <QuickStat
          icon={Clock}
          label="Duration"
          value={build.duration ? formatDuration(build.duration) : 'N/A'}
          description={build.completed_at ? `Completed ${formatRelativeTime(build.completed_at)}` : ''}
        />
      </div>

      {/* Progress (for active builds) */}
      {build.progress && isBuildActive(build.status) && (
        <div className="card p-6">
          <h2 className="text-lg font-semibold text-gray-900 mb-4">Build Progress</h2>
          <ProgressBar progress={build.progress} />
        </div>
      )}

      {/* Error message (for failed builds) */}
      {build.status === 'failed' && build.error_message && (
        <div className="card p-6 border-danger-200 bg-danger-50">
          <div className="flex items-start space-x-3">
            <AlertCircle className="w-5 h-5 text-danger-600 mt-0.5" />
            <div>
              <h3 className="text-sm font-medium text-danger-800">Build Failed</h3>
              <p className="text-sm text-danger-700 mt-1">{build.error_message}</p>
            </div>
          </div>
        </div>
      )}

      {/* Tabs */}
      <div className="card">
        <div className="border-b border-gray-200">
          <nav className="flex space-x-8 px-6">
            {[
              { id: 'overview', label: 'Overview', icon: Settings },
              { id: 'logs', label: 'Logs', icon: Terminal },
              { id: 'config', label: 'Configuration', icon: FileText },
              { id: 'artifacts', label: 'Artifacts', icon: Download },
            ].map(({ id, label, icon: Icon }) => (
              <button
                key={id}
                onClick={() => setActiveTab(id as any)}
                className={cn(
                  'flex items-center space-x-2 py-4 px-1 border-b-2 font-medium text-sm transition-colors',
                  activeTab === id
                    ? 'border-primary-500 text-primary-600'
                    : 'border-transparent text-gray-500 hover:text-gray-700 hover:border-gray-300'
                )}
              >
                <Icon className="w-4 h-4" />
                <span>{label}</span>
              </button>
            ))}
          </nav>
        </div>

        <div className="p-6">
          {activeTab === 'overview' && (
            <BuildOverview build={build} />
          )}
          
          {activeTab === 'logs' && (
            <BuildLogs
              logs={filteredLogs}
              onSearchChange={setLogSearch}
              onLevelChange={setLogLevel}
              searchTerm={logSearch}
              levelFilter={logLevel}
            />
          )}
          
          {activeTab === 'config' && (
            <BuildConfiguration build={build} />
          )}
          
          {activeTab === 'artifacts' && (
            <BuildArtifacts build={build} />
          )}
        </div>
      </div>
    </div>
  );
}

// Quick stat component
interface QuickStatProps {
  icon: React.ComponentType<{ className?: string }>;
  label: string;
  value: string;
  description?: string;
}

function QuickStat({ icon: Icon, label, value, description }: QuickStatProps) {
  return (
    <div className="card p-4">
      <div className="flex items-center space-x-3">
        <Icon className="w-5 h-5 text-gray-500" />
        <div className="min-w-0 flex-1">
          <p className="text-sm font-medium text-gray-600">{label}</p>
          <p className="text-lg font-semibold text-gray-900 truncate">{value}</p>
          {description && (
            <p className="text-xs text-gray-500 truncate">{description}</p>
          )}
        </div>
      </div>
    </div>
  );
}

// Build overview tab
function BuildOverview({ build }: { build: Build }) {
  return (
    <div className="space-y-6">
      <div className="grid grid-cols-1 lg:grid-cols-2 gap-6">
        {/* Build information */}
        <div>
          <h3 className="text-lg font-medium text-gray-900 mb-4">Build Information</h3>
          <dl className="space-y-3">
            <div className="flex justify-between">
              <dt className="text-sm font-medium text-gray-500">Build ID</dt>
              <dd className="text-sm text-gray-900 font-mono">{build.id}</dd>
            </div>
            <div className="flex justify-between">
              <dt className="text-sm font-medium text-gray-500">Status</dt>
              <dd><StatusBadge status={build.status} /></dd>
            </div>
            <div className="flex justify-between">
              <dt className="text-sm font-medium text-gray-500">Created by</dt>
              <dd className="text-sm text-gray-900">{build.created_by}</dd>
            </div>
            <div className="flex justify-between">
              <dt className="text-sm font-medium text-gray-500">Started</dt>
              <dd className="text-sm text-gray-900">
                {build.started_at ? formatDate(build.started_at) : 'Not started'}
              </dd>
            </div>
            <div className="flex justify-between">
              <dt className="text-sm font-medium text-gray-500">Completed</dt>
              <dd className="text-sm text-gray-900">
                {build.completed_at ? formatDate(build.completed_at) : 'Not completed'}
              </dd>
            </div>
            <div className="flex justify-between">
              <dt className="text-sm font-medium text-gray-500">Duration</dt>
              <dd className="text-sm text-gray-900">
                {build.duration ? formatDuration(build.duration) : 'N/A'}
              </dd>
            </div>
          </dl>
        </div>

        {/* Configuration summary */}
        <div>
          <h3 className="text-lg font-medium text-gray-900 mb-4">Configuration</h3>
          <dl className="space-y-3">
            <div className="flex justify-between">
              <dt className="text-sm font-medium text-gray-500">Platform</dt>
              <dd className="text-sm text-gray-900">{build.config.platform}</dd>
            </div>
            <div className="flex justify-between">
              <dt className="text-sm font-medium text-gray-500">Location</dt>
              <dd className="text-sm text-gray-900">{build.config.location}</dd>
            </div>
            <div className="flex justify-between">
              <dt className="text-sm font-medium text-gray-500">Spec</dt>
              <dd className="text-sm text-gray-900">{build.config.spec}</dd>
            </div>
            <div className="flex justify-between">
              <dt className="text-sm font-medium text-gray-500">Debug mode</dt>
              <dd className="text-sm text-gray-900">{build.config.debug ? 'Yes' : 'No'}</dd>
            </div>
            <div className="flex justify-between">
              <dt className="text-sm font-medium text-gray-500">Dry run</dt>
              <dd className="text-sm text-gray-900">{build.config.dry_run ? 'Yes' : 'No'}</dd>
            </div>
            {build.config.timeout && (
              <div className="flex justify-between">
                <dt className="text-sm font-medium text-gray-500">Timeout</dt>
                <dd className="text-sm text-gray-900">{formatDuration(build.config.timeout)}</dd>
              </div>
            )}
          </dl>
        </div>
      </div>

      {/* Custom variables */}
      {Object.keys(build.config.variables || {}).length > 0 && (
        <div>
          <h3 className="text-lg font-medium text-gray-900 mb-4">Custom Variables</h3>
          <div className="bg-gray-50 rounded-lg p-4">
            <pre className="text-sm text-gray-900 overflow-x-auto">
              {JSON.stringify(build.config.variables, null, 2)}
            </pre>
          </div>
        </div>
      )}
    </div>
  );
}

// Build logs tab
interface BuildLogsProps {
  logs: BuildLogEntry[];
  onSearchChange: (search: string) => void;
  onLevelChange: (level: 'all' | 'error' | 'warning' | 'info') => void;
  searchTerm: string;
  levelFilter: string;
}

function BuildLogs({ logs, onSearchChange, onLevelChange, searchTerm, levelFilter }: BuildLogsProps) {
  const handleDownloadLogs = () => {
    const logText = logs.map(log => 
      `[${log.timestamp}] [${log.level.toUpperCase()}] [${log.source}] ${log.message}`
    ).join('\n');
    
    downloadAsFile(logText, `build-logs-${Date.now()}.txt`, 'text/plain');
  };

  return (
    <div className="space-y-4">
      {/* Log controls */}
      <div className="flex flex-col sm:flex-row gap-4">
        <div className="flex-1">
          <input
            type="text"
            placeholder="Search logs..."
            className="input"
            value={searchTerm}
            onChange={(e) => onSearchChange(e.target.value)}
          />
        </div>
        <div className="flex gap-2">
          <select
            className="input w-auto"
            value={levelFilter}
            onChange={(e) => onLevelChange(e.target.value as any)}
          >
            <option value="all">All Levels</option>
            <option value="error">Errors</option>
            <option value="warning">Warnings</option>
            <option value="info">Info</option>
          </select>
          <button onClick={handleDownloadLogs} className="btn-secondary">
            <Download className="w-4 h-4 mr-2" />
            Download
          </button>
        </div>
      </div>

      {/* Log entries */}
      <div className="bg-gray-900 rounded-lg p-4 max-h-96 overflow-y-auto">
        {logs.length === 0 ? (
          <p className="text-gray-400 text-center py-8">No logs available</p>
        ) : (
          <div className="space-y-1 font-mono text-sm">
            {logs.map((log, index) => (
              <LogEntry key={index} log={log} />
            ))}
          </div>
        )}
      </div>
    </div>
  );
}

// Log entry component
function LogEntry({ log }: { log: BuildLogEntry }) {
  const levelColors = {
    debug: 'text-gray-400',
    info: 'text-blue-400',
    warning: 'text-yellow-400',
    error: 'text-red-400',
    critical: 'text-red-500',
  };

  return (
    <div className="flex space-x-3 text-xs">
      <span className="text-gray-500 whitespace-nowrap">
        {new Date(log.timestamp).toLocaleTimeString()}
      </span>
      <span className={cn('uppercase font-semibold w-8', levelColors[log.level])}>
        {log.level}
      </span>
      <span className="text-gray-400 w-16 truncate">
        {log.source}
      </span>
      <span className="text-gray-200 flex-1">
        {log.message}
      </span>
    </div>
  );
}

// Build configuration tab
function BuildConfiguration({ build }: { build: Build }) {
  const configData = {
    build_id: build.id,
    status: build.status,
    configuration: build.config,
    timestamps: {
      started_at: build.started_at,
      completed_at: build.completed_at,
    },
    metadata: {
      created_by: build.created_by,
      duration: build.duration,
    }
  };

  const handleDownloadConfig = () => {
    downloadAsFile(
      JSON.stringify(configData, null, 2),
      `build-config-${build.id.substring(0, 8)}.json`,
      'application/json'
    );
  };

  return (
    <div className="space-y-4">
      <div className="flex justify-between items-center">
        <h3 className="text-lg font-medium text-gray-900">Build Configuration</h3>
        <button onClick={handleDownloadConfig} className="btn-secondary">
          <Download className="w-4 h-4 mr-2" />
          Download JSON
        </button>
      </div>
      
      <div className="bg-gray-50 rounded-lg p-4 overflow-x-auto">
        <pre className="text-sm text-gray-900">
          {JSON.stringify(configData, null, 2)}
        </pre>
      </div>
    </div>
  );
}

// Build artifacts tab
function BuildArtifacts({ build }: { build: Build }) {
  if (build.artifacts.length === 0) {
    return (
      <div className="text-center py-12">
        <Download className="w-16 h-16 text-gray-300 mx-auto mb-4" />
        <h3 className="text-lg font-medium text-gray-900 mb-2">No Artifacts</h3>
        <p className="text-gray-600">
          {isBuildActive(build.status) 
            ? 'Artifacts will appear here when the build completes.'
            : 'This build did not produce any artifacts.'
          }
        </p>
      </div>
    );
  }

  return (
    <div className="space-y-4">
      <h3 className="text-lg font-medium text-gray-900">Build Artifacts</h3>
      
      <div className="space-y-3">
        {build.artifacts.map((artifact, index) => (
          <div key={index} className="flex items-center justify-between p-4 border border-gray-200 rounded-lg">
            <div className="flex items-center space-x-4">
              <Download className="w-8 h-8 text-gray-400" />
              <div>
                <h4 className="font-medium text-gray-900">{artifact.name}</h4>
                <p className="text-sm text-gray-600">
                  {artifact.type}  {formatFileSize(artifact.size)}
                </p>
                {artifact.checksum && (
                  <p className="text-xs text-gray-500 font-mono">
                    SHA256: {artifact.checksum.substring(0, 16)}...
                  </p>
                )}
              </div>
            </div>
            
            <div className="flex items-center space-x-2">
              <button
                onClick={() => copyToClipboard(artifact.path)}
                className="btn-sm btn-secondary"
                title="Copy path"
              >
                <Copy className="w-3 h-3" />
              </button>
              <a
                href={`/api/builds/${build.id}/artifacts/${artifact.name}/download`}
                className="btn-sm btn-primary"
                download
              >
                <Download className="w-3 h-3 mr-1" />
                Download
              </a>
            </div>
          </div>
        ))}
      </div>
    </div>
  );
}
